
import {combineReducers} from 'redux'
import {TPostsState, TStatesState, TAction, Actions} from './../types'

const postsStateInitial = {
  posts: [
    {id: 0, payload: 'ðŸ”¥Post 1'},
    {id: 1, payload: 'ðŸ˜Post 2'}
  ],
  fetchedPosts: []
}

const statesStateInitial = {
  postsPending: false,
  postsLoadingError: ''
}

const postsReducer = (state: TPostsState = postsStateInitial, action: TAction) => {
  switch(action.type) {
    case Actions['POST/PUSH']: return {...state, posts: state.posts.concat([action.post])}
    case Actions['POSTS/SUCCESS']: return {...state, fetchedPosts: action.posts}
    default: return state
  }
}
const statesReducer = (state: TStatesState = statesStateInitial, action: TAction) => {
  switch(action.type) {
    case Actions['POSTS/PENDING']: return {...state, postsPending: action.pending}
    case Actions['POSTS/FAIL']: return {...state, postsLoadingError: action.error}
    default: return state
  }
}
const reducer = combineReducers({
  posts: postsReducer,
  states: statesReducer
})

export {reducer}