
import React, { useState } from 'react'
import {connect} from 'react-redux'
import {pushPost} from '../actions'
import {TProps} from './../types'

type TPostFormState = { title: string }

const connector = connect(undefined, { pushPost })

const PostForm: React.FunctionComponent<TProps<typeof connector>> = ({pushPost}) => {

  const [[state, updateState_], none] = [useState<TPostFormState>({title: 'title'}), '']

  const updateState = (title: string, value: string = none) => {
    updateState_(state => ({...state, [title]: value}))
  }
  
  const submitEventHandler = (event: React.FormEvent) => {
    const [id, title] = [Date.now(), state.title]
    const shouldPushPost = !!title.trim()
    event.preventDefault()
    if (shouldPushPost) {
      updateState('title')
      pushPost({id, payload: title})
    }
  }

  const changeEventHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    let {name, value} = event.target
    updateState(name, value)
  }
    
  return <>
    <form onSubmit={submitEventHandler}>
      <div className="row">
        <div className="col-md-2">
          <input
            type="text"
            className="form-control form-control-sm"
            onChange={changeEventHandler}
            name="title"
            value={state.title} />
          <div className="form-text">To create post input text</div>
        </div>
        <div className="col-md-4">
          <button type="submit" className="btn btn-light btn-sm">Push</button>
        </div>
      </div>
    </form>
  </>
}

export default connector(PostForm)