
// import {ThunkAction as ThunkAction_, ThunkDispatch as ThunkDispatch_} from 'redux-thunk'
import {Action, AnyAction} from 'redux'
import {ConnectedProps} from 'react-redux'

//type ThunkAction<R> = ThunkAction_<R, TState, unknown, Action<string>>
//type ThunkDispatch = ThunkDispatch_<TState, unknown, Action<string>>

type TProps<T, O = {}> = ConnectedProps<T> & O

type TPost = {
  id: number,
  payload: string
}

type TPost2 = {
  id: number,
  title: string,
  body: string
}

type TStatesState = {
  postsPending: boolean,
  postsLoadingError: string
}

type TPostsState = {
  posts: TPost[],
  fetchedPosts: TPost2[]
}

interface TState {
  posts: TPostsState,
  states: TStatesState
}

enum Actions {
  'POST/PUSH' = 'POST/PUSH',
  'POSTS/FETCH' = 'POSTS/FETCH',
  'POSTS/PENDING' = 'POSTS/PENDING',
  'POSTS/SUCCESS' = 'POSTS/SUCCESS',
  'POSTS/FAIL' = 'POSTS/FAIL'
}

type TAction = 
{type: typeof Actions['POST/PUSH'], post: TPost} |
{type: typeof Actions['POSTS/FETCH'], limit: number} |
{type: typeof Actions['POSTS/PENDING'], pending: boolean} |
{type: typeof Actions['POSTS/SUCCESS'], posts: TPost2[]} |
{type: typeof Actions['POSTS/FAIL'], error: string}

export {Actions}
export type {
  TPost,
  TPost2, 
  //ThunkAction, 
  //ThunkDispatch, 
  TProps, 
  TPostsState,
  TStatesState,
  TState, 
  TAction}