
import {connect} from 'react-redux'
import Post2 from './Post2'
import {Spinner} from './Spinner'
import {postsFetch} from "../actions"
import {TProps, TState} from '../types'

const connector = connect(
  (state: TState) => ({
    fetchedPosts: state.posts.fetchedPosts,
    postsPending: state.states.postsPending,
    postsLoadingError: state.states.postsLoadingError
  }),
  {postsFetch}
)

const FetchPosts: React.FunctionComponent<TProps<typeof connector>> = ({
  postsFetch,
  fetchedPosts,
  postsPending,
  postsLoadingError
}) => {
  const handleClick = () => postsFetch(4)
  let elements

  if (postsPending)
    elements = <Spinner text={'Loading...'} />
  else if (postsLoadingError)
    elements = <div>{postsLoadingError}</div>
  else if (fetchedPosts.length === 0)
    elements = <div>No posts</div>
  else
    elements = fetchedPosts.map(post => <Post2 post={post} key={post.id} />)
  
  return <div>
    <button
      type="submit"
      className="btn btn-primary btn-sm"
      disabled={postsPending}
      onClick={handleClick}>Load</button>
    {elements}
  </div>
}

export default connector(FetchPosts)