
import {useParams, useHistory, useRouteMatch, Redirect} from 'react-router-dom'

interface ItemsParams {
  id?: string
}

const Items: React.FunctionComponent = () => {
  const parameters = useParams<ItemsParams>()
  const history = useHistory()
  const routeMatch = useRouteMatch()
  const clickHandler = (event: React.MouseEvent) => history.push('/items/5')
  if (parameters.id) {
    return <>{parameters.id}</>
  }
  else return <div>
    <div>UNSPECIFIED ITEM</div>
    <button
      onClick={clickHandler}
      type="submit"
      className="btn btn-light btn-sm">/items/5</button>
  </div>
}

export default Items