
import {takeEvery, put, call} from 'redux-saga/effects'
import {batchActions} from 'redux-batched-actions'
import {isEmptyObj} from '../utils/types'
import {Actions as actionTypes, TPost2} from '../types'
import * as actions from '../actions'

const fetchPosts = async (limit: number) => {
  let response = await fetch(`https://jsonplaceholder.typicode.com/posts?_limit=${limit}`)
  return {response, body: await response.json()}
}

function *sagaWorker(action: any): Generator<any, void, any> {
  const [unknownErrorMessage, HTTP404Error] = [
    'UnknownError',
    'HTTP404Error: Resource not found'
  ]
  yield put(actions.postsPending(true))
  try {
    let {response, body} = yield call(fetchPosts, action.limit)
    if (response.status === 404) {
      throw {message: HTTP404Error}
    }
    if (isEmptyObj(body)) {
      body = []
    }
    const posts: TPost2[] = body.map((item: any) => ({id: item.id, title: item.title, body: item.body}))
    yield put(batchActions([
      actions.postsSuccess(posts),
      actions.postsPending(false)
    ]))
  }
  catch(catched) {
    yield put(batchActions([
      actions.postsPending(false),
      actions.postsFail(catched.message || unknownErrorMessage)
    ]))
  }
}

export function *sagaWatcher(): Generator<any, void, any> {
  yield takeEvery(actionTypes['POSTS/FETCH'], sagaWorker)
}