
import {TPost} from './../types'

interface PostInterface {
    post: TPost
}

const Post: React.FunctionComponent<PostInterface> = ({post}) => {
    let {id, payload} = post
    return <div className="card text-white bg-dark mb-3" style={{'maxWidth': '540px'}}>
      <div className="row g-0">
        <div className="col-md-4">
          <img src={'https://cdn.iconscout.com/icon/free/png-512/react-1-282599.png'} alt={`${id}`} style={{'width': '100%'}}/>
        </div>
        <div className="col-md-8">
          <div className="card-body">
            <h5 className="card-title">{payload}</h5>
            <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
            <p className="card-text"><small className="text-muted">Last updated N mins ago</small></p>
          </div>
        </div>
      </div>
    </div>
}

export default Post