
import {TPost, TPost2, TAction, Actions} from "../types"

export const pushPost = (post: TPost): TAction => ({type: Actions['POST/PUSH'], post})


export const postsFetch = (limit: number = 5): TAction => ({type: Actions['POSTS/FETCH'], limit})

export const postsPending = (pending: boolean): TAction => ({type: Actions['POSTS/PENDING'], pending})

export const postsFail = (error: string): TAction => ({type: Actions['POSTS/FAIL'], error: error})

export const postsSuccess = (posts: TPost2[]): TAction => ({type: Actions['POSTS/SUCCESS'], posts})